// import React, { useState } from "react";
import "./form.css";
import "../../App.css";
import React, { Component } from 'react'
import { Button, Form, Container, Header } from 'semantic-ui-react'
import axios from 'axios';

export default class Formk extends Component {
  constructor(props) {
    super(props)
  
    this.state = {
       name: '',
       mobile: '',
       district: '',
       taluk: '',
       village: ''
    }
  }

  changeHandler = (e) => {
    this.setState({[e.target.name] : e.target.value})
  }

  submitHandler = e => {
    e.preventDefault();
    console.log(this.state);
    // https://sheet.best/
    axios.post('https://sheet.best/api/sheets/252845a3-ff14-47da-8474-a6914a9d1999', this.state)
    
    .then(response => {
      // console.log(response);
      this.useNavigate("/login")
      
    })
  }
  
  render() {
    const { name, mobile, district, taluk, village } = this.state;    
    return (
      <Container fluid className="container">
        <Header as='h2'>React Google Sheets!</Header>
        <Form className="form" onSubmit={() => {
          this.submitHandler();
          this.useNavigate("/voter");
        }}>
          <Form.Field>
            <label>Name</label>
            <input placeholder='Enter your name' type="text" name = "name" value = {name} onChange={this.changeHandler}/>
          </Form.Field>
          <Form.Field>
            <label>Mobile number</label>
            <input placeholder='Enter your Mobile number' type="number" name = "mobile" value = {mobile} onChange={this.changeHandler}/>
          </Form.Field>
          <Form.Field>
            <label>District</label>
            <input placeholder='Enter your salary' type="text" name = "district" value = {district} onChange={this.changeHandler}/>
          </Form.Field>
          <Form.Field>
            <label>Taluk</label>
            <input placeholder='Enter your Taluk' type="text" name = "taluk" value = {taluk} onChange={this.changeHandler}/>
          </Form.Field>
          <Form.Field>
            <label>Village</label>
            <input placeholder='Enter your Village' type="text" name = "village" value = {village} onChange={this.changeHandler}/>
          </Form.Field>
          
          <Button color="blue" type='submit'>Submit</Button>
        </Form>
      </Container>
    )
  }
}