import React, { useState} from "react";
import "./admin.css";
import "../../App.css";
import { Chart } from "react-google-charts";
import axios from 'axios'
// import Select from "react-dropdown-select";


// var jd = require('jsdataframe');




const Admin = () => {

  // const [data, setData] = useState([]);

  // It state will contain the error when
  // correct file extension is not used
  // const [error, setError] = useState("");
  const [alldata, setalldata] = useState();
  const [allcol, setallcol] = useState();
  const [dfx, setdf] = useState();
  const [grph1, sgrph1] = useState();



  // const handlelist = () => {

  // }
  // var full_val = {}

  const handleParse = () => {
    //  handleFileChange();

    const url = "https://docs.google.com/spreadsheets/d/e/2PACX-1vQr1fqTQ1FAXkT9xM-wr6zIh38nnOizdm-qrOb5nzEyqFvpYAJiqm9ESajGf_bnM1ybQtLwxa1s9AtI/pub?gid=1743853158&single=true&output=csv"

  //   axios.get(url)
  // .then(function (response) {
  //   console.log("axis1---------",response.data);                                                                                                                                                    
  // })
  // .catch(function (error) {
  //   console.log(error);                                                                                                                                                       
  // });

    axios.get(url, {
      responseType: 'blob',
    })
      .then((res) => {
        // console.log(res)
        const file = res.data;
        // console.log(file)
        file.text().then((csvStr) => {
          const jsonObj = csvJSON(csvStr);
          // console.log(jsonObj)
          const all_cols = Object.keys(jsonObj[0]);
          all_cols.splice(0, 1);
          // console.log(all_cols)

          var final_val = eachCol(jsonObj, all_cols)

          let col_ob = []
          col_ob.push({ "id": "Select Question", "value": "Select Question" })
          all_cols.forEach((yt) => {
            let qw = {}
            qw["id"] = yt
            qw["value"] = yt
            col_ob.push(qw)
          })

          setallcol(col_ob)
          // full_val = final_val
          setalldata(final_val)

   
        /*   {

      
        
         }*/
          // sgrph1(final_val[all_cols[2]])


          // const options = {
          //   title: final_val[all_cols[2]],
          // };

          function eachCol(arry, cols) {
            let for_g = []
            cols.forEach((zz) => {
              const vls = findOcc(arry, zz)
              let hj = []
              hj.push([zz, "responses"])
              vls.forEach((vb) => {
                let dg = [vb[zz], vb["occurrence"]]
                hj.push(dg)
              })
              for_g[zz] = hj
            })
            return for_g
          }
        })
      })

    function csvJSON(csvStr) {
      var lines = csvStr.split("\n");
      var result = [];

      // NOTE: If your columns contain commas in their values, you'll need
      // to deal with those before doing the next step 
      // (you might convert them to &&& or something, then covert them back later)
      // jsfiddle showing the issue https://jsfiddle.net/
      var headers = lines[0].split(",");

      for (var i = 1; i < lines.length; i++) {

        var obj = {};
        var currentline = lines[i].split(",");

        for (var j = 0; j < headers.length; j++) {
          obj[headers[j]] = currentline[j];
        }
        // console.log('obj--> ',obj)

        if (i > 2) {
          result.push(obj);
        }


      }
      return result; //JavaScript object
    }



    function findOcc(arr, key) {
      let arr2 = [];

      arr.forEach((x) => {
        if (arr2.some((val) => { return val[key] === x[key] })) {
          // If yes! then increase the occurrence by 1
          arr2.forEach((k) => {
            if (k[key] === x[key]) {
              k["occurrence"]++
            }
          })

        } else {
          let a = {}
          a[key] = x[key]
          a["occurrence"] = 1
          arr2.push(a);
        }
      })
      return arr2
    }

  };


  const handleChange = e => {

    let kl = alldata
    // console.log(kl)
    sgrph1(kl[e.target.value])
    setdf(e.target.value)
  }

  // Config variables

  // const datac = [
  //   ["Task", "Hours per Day"],
  //   ["C. ಏನ್ ಹೇಳಲುಬಯಸುವುದಿಲ್ಲ", 11],
  //   ["A.ಹೌದು", 2],
  //   ["Commute", 2],
  //   ["Watch TV", 2],
  //   ["Sleep", 7],
  // ];
  const options = {
    title: dfx,
  };

  const options_bar = {
    title: dfx,
    colors: ["purple","blue","orange","green"]
  };

  function Options({ options }) {
    return (
      options.map(option =>
        <option key={option.id} value={option.value}>
          {option.value}
        </option>)
    );
  }

  return (
    <div className="container-fluid">
      <div className="row">
        <div className="container-fluid bg-dark text-dark">
          <div className="row">
            <h1>. </h1>
          </div>
        </div>
      </div>

      <div className="p-4">
        <div className="progress" style={{ height: "0.2rem" }}>
          <div
            className="progress-bar"
            role="progressbar"
            style={{ width: "100%" }}
            aria-valuenow="100"
            aria-valuemin="0"
            aria-valuemax="100"
          ></div>
        </div>
      </div>

      <div className="container-fluid row p-3">
        <button className="form-control" onClick={handleParse}><b>Click here to generate Charts</b></button>
      </div>

      {allcol &&
        <div className="container-fluid row">
          <select 
            name="Options"
            className="form-control" onChange={handleChange}>
            <Options options={allcol} />
          </select>
        </div>
      }

      <div>
        {dfx}
      </div>

      {grph1 &&
        <div>
          <div className="container-fluid row">
            <Chart
              chartType="PieChart"
              data={grph1}
              options={options}
              width={"100%"}
              height={"400px"}
            />
          </div>

          <div className="container-fluid row">
            <Chart chartType="BarChart" width="100%" height="400px" data={grph1} options={options_bar}/>
          </div>
        </div>
      }

    </div>
  );
};

export default Admin;
